/* the container that wraps around everything */
.breather-container{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 300px;
    width: 300px;
    margin: 0;
    /* margin: auto; */
    /* to position absolute within the container */
    position: relative;
    /* to help the container expand and contract */
    -webkit-transform: scale(1);
            transform: scale(1);
}

/* putting gradient circle first because it is going to be behind the solid circle */
.gradient-circle{
  background: conic-gradient(
      #900C3F 0%,
      #581845  40%,
     #3C356D   40%,
     #2B2457    60%,
      #B781AB 60%,
      #9C6390  100%
  ) ;
  height: 320px;
  width: 320px;
  position: absolute;
  top: -10px;
  left: -10px;
  z-index: -2;
  border-radius: 50%;
}

/* the solid colour circle */
.circle{ 
   background-color: #130E37;
    height: 100%;
    width: 100%;
     position: absolute;
      top: 0;
     left: 0; 
      z-index: -1;
      border-radius: 50%;   
}

.initial-circle{
 background-color:#531d34;
    height: 100%;
    width: 100%;
     position: absolute;
      top: 0;
     left: 0; 
      z-index: -1;
      border-radius: 50%;   
}

.pointer-container{
    position: absolute;
    top: -40px;
    left: 140px;
    width: 20px;
    height: 190px;
    /* place rotation animation on the container and match the time from the 7 seconds in js */
    -webkit-animation: rotate 7.5s linear forwards infinite;
            animation: rotate 7.5s linear forwards infinite;
    -webkit-transform-origin: bottom center;
            transform-origin: bottom center;
}

.pointer{
    background-color: white;
    border-radius: 50%;
    height: 14px;
    width: 14px;
    display:block;
}

#text{
    font-size: 1.5em;
}

.expand{
    -webkit-animation: expand 3s linear forwards;
            animation: expand 3s linear forwards;  
}

.shrink{
    -webkit-animation: shrink 3s linear forwards;
            animation: shrink 3s linear forwards;  
}

.hold{
    -webkit-transform: scale(1.2);
            transform: scale(1.2)   
}

.getready{
   color:white;
}

/* keyframes tell the animation what to do */
@-webkit-keyframes expand{
     from{
       -webkit-transform: scale(1);
               transform: scale(1)
     }

     to{
         -webkit-transform: scale(1.2);
                 transform: scale(1.2)
     }
}
@keyframes expand{
     from{
       -webkit-transform: scale(1);
               transform: scale(1)
     }

     to{
         -webkit-transform: scale(1.2);
                 transform: scale(1.2)
     }
}

@-webkit-keyframes shrink{
     from{
        -webkit-transform: scale(1.2);
                transform: scale(1.2)
     }

     to{
        -webkit-transform: scale(1);
                transform: scale(1)
     }
}

@keyframes shrink{
     from{
        -webkit-transform: scale(1.2);
                transform: scale(1.2)
     }

     to{
        -webkit-transform: scale(1);
                transform: scale(1)
     }
}

@-webkit-keyframes rotate{
    /* starting with a rotation postion */
    from{
       -webkit-transform: rotate(0deg);
               transform: rotate(0deg)
    }

    to{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
    }
}

@keyframes rotate{
    /* starting with a rotation postion */
    from{
       -webkit-transform: rotate(0deg);
               transform: rotate(0deg)
    }

    to{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
    }
};
